// src/router/index.js
import Vue from 'vue';
import Router from 'vue-router';
import HomeView from '../components/HomeView.vue'
import ResistResults from '../views/ResistResults.vue'
import SearchResults from '../views/SearchResults.vue'


Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: HomeView
    },
    {
        path: '/resist-results', 
        name: 'resist-results',
        component: ResistResults,
        props: true
      },
      {
        path: '/search-results', 
        name: 'search-results',
        component: SearchResults,
        props: true
      }
  ]
});


